    articleCaption3: '',
    textCopy: '  <p style="text-indent:0"><span class="first-letter">T</span>his past year at UCLA’s Electrical and Computer Engineering Department has yet again been marked by the remarkable achievements of our students and faculty. UCLA ECE continues to impact the greater electrical and computer engineering community with our education, service, and research efforts.</p><p>We welcomed three new faculty this past year. Prof. Liz Izhikevich, who obtained her degree from Stanford University, is a pioneer on network security through examining network traffic and characteristics using innovative “scanning” methods and algorithms. Prof. Di Luo, who came us from University of Illinois Urbana-Champagne, works broadly in the use of computation in solving problems in quantum science. He uses machine learning (ML) and artificial intelligence (AI) algorithms to make modifications to AI algorithms and explores quantum algorithms for optimization. And, Prof. Leo Zhou, fresh from his post-doctoral research at CalTech, bridges information theory (statistical sampling theory), optimization, and theoretical physics and has been able to combine them to identify problems that highlights the computational advantage of quantum computing, and realization of these quantum applications in real physical systems.</p><p>We also welcomed back two of our faculty from their service (Intergovernmental Personnel Act) to the U.S. government. Prof. Jason Woo completed his four years as a Program Manager for DARPA’s Microsystems Technology Office and Prof. Subramanian Iyer completed one year as the Director of National Advanced Packaging and Manufacturing Program. Both of them were seminal in guiding the advancement of the semiconductor manufacturing and research of our nation.</p><p>Members of our UCLA community have been recognized by the engineering community at large. To highlight a few, Prof. Aydogan Ozcan, for his contributions in photonics, Prof. Lixia Zhang, for her contributions in computer networking, and Adj. Professor Dariush Divsalar, for his contribution at NASA\'s Jet Propulsion Laboratory, were elected to the National Academy of Engineering, the highest honor in an engineer career. Assistant Professor Nader Sehatbakhsh received the National Science Foundation CAREER award for “Integrating Microarchitecture Simulation and Side-Channel Leakage Modeling for Safer Software”. For teaching, Associate Professor Jonathan Kao received Northrop Grumman Excellence in Teaching Award. Prof. Behzad Razavi was awarded funds to establish the Center for Education of Microchip Designers (CEMiD) by the National Center for the Advancement of Semiconductor Technology (NatCAST). Our students were also recognized. Professor Aydin Babakhani\'s student, Sidharth Thomas, received the IEEE Solid-State Circuits Society (SSCS) Predoctoral Achievement Award. Haoxiang Ren and Fulian Hung who work with Prof. Subramanian Iyer and C.K. Ken Yang won the Qualcomm Innovation Fellowship. And, our IEEE student group was recognized with IEEE Rising Star for Introductory Projects for their work on “Open Project Space” and for Advanced Project for “WRAP RF Design Systems”.</p><p>This report also highlights recent work funded by DARPA on Optimum Processing Technology Inside Memory Arrays (OPTIMA) led by Prof. Sudhakar Pamarti using energy-efficient analog computing for neural net computation. Programs like this are just one example of the multitude of research efforts that amounts to grant funding of over $35M in the past year in the department.</p><p>This annual report and our website reflect a change in how we organize our research that was initiated this past year. The new series of tracks better illustrate the collaborative and multi-disciplinary nature of ECE research and education engaged by our students and faculty. As my tenure as chairman comes to a close, I pen this final introductory message of an annual report and am pleased to be passing the chairman\’s duties to Prof. Benjamin Williams. The past five years have been a whirlwind and I am excited by the new landscape of our field and the energy that our department is bringing to the ECE profession.</p>'
