    articleCaption2: '',
    articleCaption3: '',
    textCopy: '<p style="text-indent:0"><span class="first-letter">A</span>rtificial Intelligence (AI) and quantum computation are transforming the landscape of science and technology, offering unprecedented tools for solving complex problems. AI has revolutionized fields such as image recognition, natural language processing, and autonomous systems by enabling machines to learn from data and make intelligent decisions. At the same time, quantum computation promises to surpass classical limits by leveraging quantum mechanical principles to perform certain computations exponentially faster. Together, AI and quantum computing not only open new frontiers in materials discovery, drug design, and optimization but also challenge and enrich our fundamental understanding of computation, learning, and physical laws.</p><p>My research is at the intersection of AI, science, and quantum computation, aiming to develop intelligent systems that advance both scientific discovery and computational capability. I design machine learning and quantum algorithms that integrate physical principles to model complex systems, accelerate simulations, and solve high-impact problems in science and engineering. At the same time, I explore how mathematical structures and quantum theory can inspire novel AI architectures and robotics algorithms with enhanced generalization, interpretability, and adaptability. By bridging AI, physics, and quantum computing, my work drives innovation across disciplines and opens new directions for both scientific understanding and intelligent technology.</p><p><span><b>Di Luo</b></span>joined UCLA Samueli in July 2024 as an assistant professor of electrical and computer engineering, and is a member of the Center for Quantum Science and Engineering, which was created by UCLA Samueli and the UCLA Division of Physical Sciences. Prior to this appointment, Luo was a fellow at the National Science Foundation-funded Institute for Artificial Intelligence and Fundamental Interactions, which is affiliated with the Center for Theoretical Physics at the Massachusetts Institute of Technology and the Physics Department of Harvard University. He received a Ph.D. in physics from the University of Illinois Urbana-Champaign (UIUC), where he also received two master\’s degrees in mathematics and physics, respectively. During his graduate studies, Luo held internships with Google Quantum AI, the Flatiron Institute and the Vector Institute. Luo was the recipient of a Charles P. Slichter Fellowship and an Excellence in Teaching Award from UIUC. He earned his bachelor\’s degree in physics and mathematics from The University of Hong Kong.</p><p></p>'
