    articleCaption3: '',
    textCopy: '  <p style="text-indent:0"><span class="first-letter">E</span>ver since physicists identified the transformative potential of computers exploiting quantum effects in the 1980s, the field of quantum information science has revolutionized both our understanding of Nature and our belief of what is computationally possible. Quantum computers are now widely believed to solve certain problems exponentially faster than classical computers. However, the full scope of quantum computing’s power and the real-world applications where it can provide a meaningful “quantum advantage” remain key questions. At the forefront of this exploration is the intersection of quantum computation and physics, where novel algorithms could unlock groundbreaking capabilities in optimization, simulation, and machine learning.</p><p>My research group focuses on understanding and harnessing the capabilities of quantum computation to address fundamental and applied questions in computer science and physical systems. We aim to develop quantum algorithms that solve computationally hard problems, such as those in optimization, logistics, quantum chemistry, and materials science, in addition to studying the theoretical limitations of these methods. By bridging quantum information science, mathematical optimization, and statistical inference, our work seeks to uncover new applications of quantum algorithms and establish a solid theoretical foundation for their performance.</p><p>Currently, we are working on several exciting projects, including quantum optimization algorithms for hard combinatorial optimization problems, quantum simulations of many-body physics, and addressing computational-statistical gaps in high-dimensional data analysis. A key emphasis of our work is connecting theory with practice: we collaborate with partners in academia and industry (including Google, QuEra, Phasecraft, and BlueQubit) to implement quantum algorithms on near-term quantum hardware, such as superconducting qubits, trapped ions, and neutral atom platforms. These efforts aim to push the boundaries of computational feasibility and pave the way for demonstrating practical quantum advantages.</p><p><span>Leo Zhou  </span>  is an Assistant Professor of Electrical and Computer Engineering at the University of California, Los Angeles. He received his Ph.D. in Physics from Harvard University in 2021 under the guidance of Mikhail Lukin, after earning a B.Sc. in Physics and Mathematics at MIT in 2014. Before joining UCLA, he held a Burke Institute Prize Fellowship at Caltech and received the Outstanding Paper Award in 2022 at the 17th Conference on Theory of Quantum Computation. His research contributions include elucidating the advantages and limitations of quantum optimization algorithms and establishing foundational results in quantum complexity theory. He has collaborated closely with experimental groups to develop and test quantum applications on cutting-edge quantum devices. Through his research, he aims to deepen our understanding of quantum algorithms and their real-world impact while mentoring the next generation of quantum engineers and scientists.</p>'
