    textCopy: '<p style="text-indent:0"><span class="first-letter">A</span>ssistant Professor Sergio Carbajo on receiving the Humboldt Research Award. Alexander von Humboldt (1769-1859) was a nature researcher and explorer, universal genius and cosmopolitan, scientist and patron. His lengthy Latin American journey from 1799 to 1804 was celebrated as the second scientific discovery of South America.</p><p>The Alexander von Humboldt Foundation links Germany to the knowledge of the world. In global competition for the topmost experts, it offers various programmes to attract researchers at different stages of their careers. Support involves both funding and personal support in all matters relating to a stay in Germany and to subsequent cooperations. The Alexander von Humboldt Foundation offers further sponsorship in the framework of its alumni programs. In this way, an active international network of more than 30,000 researchers has evolved since the Foundation was established in 1953.<p></p>The Humboldt Research Awards have been a central pillar of the Alexander von Humboldt Foundation’s sponsorship activities since 1972. The awards honour the academic achievements to date of internationally recognized scientists and scholars from abroad. A not insignificant number of award winners has subsequently been granted the Nobel Prize. The research award programmes take particular account of the needs of the international academic elite. The award winners are invited to spend time for research in Germany and are able to organize their stay on a flexible basis.</p>'
