    articleImageAltTextProf: 'UCLA ECE Faculty Sudhakar Pamarti, Ken Yang',
    articleImageCred: 'Illustration by Pablo Stanley',
    articleAuthor: 'From breakingdefense.com, Sydney J. Freedberg Jr.',
    articleCaption1: 'Professors Sudhakar Pamarti, Puneet Gupta and Ken Yang received a DARPA Award OPTIMA',
    articleCaption2: '',
    articleCaption3: '',
    textCopy: '  <p style="text-indent:0"><span class="first-letter">T</span>he ongoing revolution in artificial intelligence takes a ton of computing power — which, in turn, consumes a lot of electrical power and generates a lot of heat.</p><p>As AI software grows more powerful and more complex, that becomes a problem even in large, air-conditioned data centers with direct connections to the electrical grid. On what the military calls the “tactical edge” — the drones, manned aircraft, ground vehicles, forward command-posts, and individual kit that have to work in Ukrainian mud, Arctic snow, or desert sands — making an AI compact enough to function can be impossible.</p><p>To bypass this bottleneck of size, weight, power, and cooling, the Pentagon\’s branch for high-risk, high-reward R&D outfit known as DARPA launched a program in December known as OPTIMA, with awards to tech giant IBM, German semiconductor maker Infineon, and three US universities, Princeton, Georgia Tech, and UCLA. If DARPA exercises all its contract options, the total value could reach $78 million over four and a half years, allocating $8 million to UCLA. OPTIMA stands for “Optimum Processing Technology Inside Memory Arrays.”</p><p>As AI grows ever more powerful and power-hungry, tech pioneers keep seeking new and better kinds of chips, evolving from CPUs to the current array of of GPUs, TPUs, NPUs, FPGAs, and more. But all these different chips have a lot in common. In particular, they all have their memory on one part of the chip where they store data and their processor on another part where calculations are done on that data. That means every computation requires moving bits back and forth between the two zones. For most applications, for most of the history of computing, the tiny amount of time and energy required to make these minuscule movements didn\’t matter.</p><p>The UCLA team is using transistors instead of capacitors on their research. This is a approach that doesn\’t require exotic hardware. In fact, UCLA is using a widely-available transistor called the 22FDX.</p><p>“22FDX is actually an older model,” according to UCLA Professor Sudhakar Pamarti. But unlike most transistors, it can change something called its threshold voltage. Over years of work, Pamarti\’s team learned how to control those variations so precisely they can use them to encode data — over 50 times more compactly than alternative approaches, he said.</p><p>What if that threshold voltage glitches and garbles the data it\’s encoding? The UCLA team uses a calibration technique called “write-verify-write,” making tiny adjustments and then checking the output. This is “time consuming,” Pamarti acknowledged, but for most applications, you only have to do it once. IBM, Infineon, Georgia Tech and Priceton are working independently on their own models to fulfill their OPTIMA contract. Professors Puneet Gupta and Ken Yang join professors Parmarti leading the research at the Electrical Engineering Department.</p>'
