    textCopy: '  <div class="grid-layout"><div class="page-text"><p style="text-indent: 0;"> <span class="first-letter"> IEEE WATT </span>  (Women Advancing Tech through Teamwork) is a technical advocacy group for women and minorities in Electrical Engineering, operating as a branch of IEEE. We empower both students at UCLA and younger girls in our community.</p><p>Throughout the year, WATT hosts a number of social, outreach, technical, mental health/advocacy, and professional workshops. We partner with K-12 schools in LA county to teach electrical engineering and programming courses, host 2-3 quarterly technical workshops for UCLA students, promote mental health and wellness through partnerships with other UCLA organizations, and invite company sponsors for info sessions/recruiting events. Additionally, WATT has a mentorship program, HogWATTs, in which we partner students one on one in order to help new incoming students get acclimated at UCLA. For more information, check us out at <span><a target="_blank" rel="noopener" href="http://www.ieeewatt.ucla.edu">http://www.ieeewatt.ucla.edu</a></span>.</p><p></p><div class="grid-cards"><div class="student-center-frame"><img src="/2023-2025/img/ece-watt1.jpg"></div><div class="student-center-frame"><img src="/2023-2025/img/ece-watt2.jpg"></div></div></div></div>    '
