    textCopy: ' <div><div class="grid-layout"><div class="full"><h3 class="page-title">Artificial Intelligence, Data Science, and Machine Learning</h3><p></p><div class="page-text"><p style="text-indent:0"><span class="first-letter">Developing </span> advanced algorithms and computational models that enable machines to learn from data, make predictions, and automate decision-making processes. This field combines principles of statistics, mathematics, and computing to design intelligent and efficient systems that can analyze vast datasets, recognize patterns, and adapt to new information. Applications include healthcare diagnostics, vision, autonomous systems, expert systems, and communication networks.</p></div></div><div class="full"><div class="prof-pics"><div><img class="fac" src="/img/AbeerAlwan-profile.jpg" alt="UCLA ECE Faculty  Alwan Abeer"></div><div><img class="fac" src="/img/Anthony-Chen.jpg" alt="UCLA ECE Faculty Anthony Chen"></div><div><img class="fac" src="/img/Diggavi.jpg" alt="UCLA ECE Faculty Diggavi"></div><div><img class="fac" src="/img/Dolecek_Lara.jpg" alt="UCLA ECE Faculty Dolecek Lara"></div><div><img class="fac" src="/img/emaminejad6.jpg" alt="UCLA ECE Faculty Sam Emaminejad"></div><div><img class="fac" src="/img/Fragouli-Teaching.jpg" alt="UCLA ECE Faculty  Christina Fragouli"></div><div><img class="fac" src="/img/gupta.jpg" alt="UCLA ECE Faculty Puneet Gupta"></div><div><img class="fac" src="/img/Lei.jpg" alt="UCLA ECE Faculty Lei He"></div><div><img class="fac" src="/img/Achuta-Kadambi.jpg" alt="UCLA ECE Faculty kadabi"></div><div><img class="fac" src="/img/JonathanKao.jpg" alt="UCLA ECE Faculty  Jonathan Kao"></div><div><img class="fac" src="/img/DiLuo_portrait.jpg" alt="UCLA ECE Faculty  Di Luo"></div><div><img class="fac" src="/img/ozcan_nai3.jpg" alt="UCLA ECE Faculty Aydogan Ocan"></div><div><img class="fac" src="/img/pamarti.jpg" alt="UCLA ECE Faculty Pamarti"></div><div><img class="fac" src="/img/Roychouwdhury.jpg" alt="UCLA ECE Faculty Roychouwdhury"></div><div><img class="fac" src="/img/Nader-Sehatbakhsh_portrait.jpg" alt="UCLA ECE Faculty Nader Sehatbakhsh"></div><div><img class="fac" src="/img/Mani-Srivastava.jpg" alt="UCLA ECE Faculty Mani Sirivastava"></div><div><img class="fac" src="/img/Tabuada-Headshot.jpg" alt="UCLA ECE Faculty Tabuada"></div><div><img class="fac" src="/img/Yuan-Tian.jpg" alt="UCLA ECE Faculty Yuan Tian"></div><div><img class="fac" src="/img/vandenberghe.jpg" alt="UCLA ECE Faculty Lieven Vandenberghe"></div><div><img class="fac" src="/img/Villasenor.jpg" alt="UCLA ECE Faculty John Villasenor"></div><div><img class="fac" src="/img/ckkenyang.jpg" alt="UCLA ECE Faculty Yang Ken "></div><div><img class="fac" src="/img/LinFYang.jpg" alt="UCLA ECE Faculty Lin Yang"></div><div><img class="fac" src="/img/yangzhang.jpg" alt="UCLA ECE Faculty Yang Zhang"></div><div><img class="fac" src="/img/leo-zhou.jpg" alt="UCLA ECE Faculty Leo Zhou"></div></div><div class="page-caption faculty-caption"><b>Faculty:</b>Alwan Abeer, Anthony Chen, Suhas Diggavi, Lara Dolecek, Sam Emaminejad, Christina Fragouli, Puneet Gupta, Lei He, Achuta Kadabi, Jonathan Kao, Di Luo, Aydogan Ocan, Pamarti, Vwani Roychouwdhury, Nader Sehatbakhsh, Mani Sirivastava, Paolo Tabuada, Yuan Tian, Lieven Vandenberghe, John Villasenor, C.K Ken Yang, Lin Yang, Yang Zhang, Leo Zhou.</div></div></div><div class="grid-layout"><div class="full"><h3 class="page-title">Human-Cyber-Physical & IoT Systems</h3><div class="page-text"><p style="text-indent:0"><span class="first-letter">Modern computer systems </span> interact intelligently and in multiple modalities with humans and the physical world as part of perception-cognition-communication-action loops. Moreover, they often do so in real-time (throughput, latency), under limited resources (computation, energy, connectivity, user attention and motor capabilities), take diverse form factors (mobile, wearable, embedded, etc.), operate wirelessly, and in challenging environments (extreme, dynamic, adversarial). They also need to be usable, ensuring that users can effectively and efficiently interact with the system while minimizing cognitive and physical effort. Lastly, these systems need operate in a trustworthy manner, meeting requirements relating to safety, resiliency, reliability, security, etc. The focus of this topic is on foundational concepts, design principles, and technologies necessary for systematic design and evaluation of such systems across the entire stack from hardware and software platforms to algorithms and applications.</p></div></div><div class="full"><div class="prof-pics"><div><img class="fac" src="/img/Anthony-Chen.jpg" alt="UCLA ECE Faculty Anthony Chen"></div><div><img class="fac" src="/img/emaminejad6.jpg" alt="UCLA ECE Faculty Sam Emaminejad"></div><div><img class="fac" src="/img/JonathanKao.jpg" alt="UCLA ECE Faculty  Jonathan Kao"></div><div><img class="fac" src="/img/Roychouwdhury.jpg" alt="UCLA ECE Faculty Vwani P. Roychouwdhury"></div><div><img class="fac" src="/img/Nader-Sehatbakhsh_portrait.jpg" alt="UCLA ECE Faculty Nader Sehatbakhsh"></div><div><img class="fac" src="/img/Mani-Srivastava.jpg" alt="UCLA ECE Faculty Mani Srivastava"></div><div><img class="fac" src="/img/Yuan-Tian.jpg" alt="UCLA ECE Faculty Yuan Tian"></div><div><img class="fac" src="/img/Villasenor.jpg" alt="UCLA ECE Faculty John Villasenor"></div><div><img class="fac" src="/img/yangzhang.jpg" alt="UCLA ECE Faculty Yang Zhang"></div></div><div class="page-caption faculty-caption"><b>Faculty:</b>Anthony Chen, Sam Emaminejad, Jonathan Kao, Vwani Roychouwdhury, Nader Sehatbakhsh, Mani Sirivastava, Yuan Tian, John Villasenor, Yang Zhang.</div></div></div><div class="grid-layout"><div class="full"><h3 class="page-title">Computer Systems Architecture & Design</h3><div class="page-text"><p style="text-indent:0"><span class="first-letter">Developing innovative </span> hardware architectures and automated design tools that enhance the efficiency, performance, and scalability. This field involves creating advanced processing architectures, memory systems, and interconnects especially leveraging application specific features. The field also includes software tools that streamline the design and optimization of these systems by addressing energy efficiency, design time and hardware-software co-design. This research drives the evolution of next-generation computing platforms, from embedded systems to large-scale data centers.</p></div></div><div class="full"><div class="prof-pics"><div><img class="fac" src="/img/Frank_Chang.jpg" alt="UCLA ECE Faculty M.-C. Frank Chang"></div><div><img class="fac" src="/img/gupta.jpg" alt="UCLA ECE Faculty Puneet Gupta"></div><div><img class="fac" src="/img/Lei.jpg" alt="UCLA ECE Faculty Lei He"></div><div><img class="fac" src="/img/DiLuo_portrait.jpg" alt="UCLA ECE Faculty Di Luo"></div><div><img class="fac" src="/img/markovic5.jpg" alt="UCLA ECE Faculty Dejan Markovic"></div><div><img class="fac" src="/img/Nader-Sehatbakhsh.jpg" alt="UCLA ECE Faculty Nader Sehatbakhsh"></div><div><img class="fac" src="/img/Villasenor.jpg" alt="UCLA ECE Faculty John Villasenor"></div><div><img class="fac" src="/img/leo-zhou.jpg" alt="UCLA ECE Faculty Leo Zhou"></div></div><div class="page-caption faculty-caption"><b>Faculty:</b>M.-C. Frank Chang, Puneet Gupta, Lei He, Di Luo, Dejan Markovic, Nader Sehatbakhsh, John Villasenor, Leo Zhou.</div></div></div><div class="grid-layout"><div class="full"><h3 class="page-title">Secure & Trustworthy Systems</h3><p></p><div class="page-text"><p style="text-indent:0"><span class="first-letter">Safeguarding digital systems </span> against threats and ensuring the reliability and integrity of computing environments is an essential element of modern computing especially in light of the expanding interaction between humans and digital devices. This field encompasses the design of intuitive user interfaces and experiences, secure hardware, software, and communication protocols that protect against cyberattacks, data breaches, and system failures. Researchers in this area work on improving accessibility, developing cryptographic methods, intrusion detection systems, and resilient architectures that enhance the trustworthiness of critical infrastructure, wearables and IoT devices, and cloud computing platforms, ultimately aiming to create robust systems that users can connect in an increasingly digital world.</p></div></div><div class="full"><div class="prof-pics"><div><img class="fac" src="/img/Diggavi.jpg" alt="UCLA ECE Faculty Diggavi"></div><div><img class="fac" src="/img/Fragouli-Teaching.jpg" alt="UCLA ECE Faculty  Christina Fragouli"></div><div><img class="fac" src="/img/Liz-Izhikevich-portrait.jpg" alt="UCLA ECE Faculty Liz Izhikevich"></div><div><img class="fac" src="/img/Roychouwdhury.jpg" alt="UCLA ECE Faculty Vwani P. Roychouwdhury"></div><div><img class="fac" src="/img/Nader-Sehatbakhsh.jpg" alt="UCLA ECE Faculty Nader Sehatbakhsh"></div><div><img class="fac" src="/img/Mani-Srivastava.jpg" alt="UCLA ECE Faculty Mani Srivastava"></div><div><img class="fac" src="/img/Tabuada-Headshot.jpg" alt="UCLA ECE Faculty Paulo Tabuada"></div><div><img class="fac" src="/img/Yuan-Tian.jpg" alt="UCLA ECE Faculty Yuan Tian"></div><div><img class="fac" src="/img/Villasenor.jpg" alt="UCLA ECE Faculty John Villasenor"></div></div><div class="page-caption faculty-caption"><b>Faculty:</b>Suhas Diggavi, Christina Fragouli, Liz Izhikevich, Vwani P. Roychouwdhury, Nader Sehatbakhsh, Mani Srivastava, Paulo Tabuada, Yuan Tian, John Villasenor. </div></div></div></div> '
