    textCopy: '  <div><div class="grid-layout"><div class="full"><h3 class="page-title">Power and Energy Storage Systems</h3><p></p><div class="page-text"><p style="text-indent:0"><span class="first-letter">Developing and optimizing </span> technologies for generating, distributing, and storing electrical energy. This field covers a broad spectrum, including renewable energy integration, smart grid advancements, and innovative battery technologies that enhance energy efficiency and reliability. Researchers work on improving the performance and lifespan of energy storage systems, developing advanced power electronics, and designing sustainable solutions for the next generation of electrical infrastructure. The goal is to support the transition to a more resilient, efficient, and environmentally friendly energy landscape.</p></div></div><div class="full"><div class="prof-pics"><div><img class="fac" src="/img/Xiaofan-Cui-portrait.jpg" alt="UCLA ECE Faculty Xiaofan Cui"></div><div><img class="fac" src="/img/emaminejad6.jpg" alt="UCLA ECE Faculty Sam Emaminejad"></div><div><img class="fac" src="/img/Subramanian_Iyer.jpg" alt="UCLA ECE Faculty Subramanian S. Iyer"></div><div><img class="fac" src="/img/Joshi.jpg" alt="UCLA ECE Faculty Chan Joshi"></div><div><img class="fac" src="/img/Tabuada-Headshot.jpg" alt="UCLA ECE Faculty Paulo Tabuada"></div><div><img class="fac" src="/img/wang.jpg" alt="UCLA ECE Faculty Kang Wang"></div><div><img class="fac" src="/img/Woo.jpg" alt="UCLA ECE Faculty Jason Woo"></div><div><img class="fac" src="/img/ckkenyang.jpg" alt="UCLA ECE Faculty C.-K. Ken Yang"></div></div><div class="page-caption faculty-caption"><b>Faculty: </b> Xiaofan Cu, Sam Emaminejad, Subramanian S. Iyer, Chan Joshi, Paulo Tabuada, Kang Wang, Jason Woo, C.-K. Ken Yang.</div></div></div><div class="grid-layout"><div class="full"><h3 class="page-title">Integrated Circuits — Analog/Digital/Mixed-Signal</h3><p></p><div class="page-text"><p style="text-indent:0"><span class="first-letter">Designing and optimizing </span> of integrated circuits (ICs) that combine analog, digital, and mixed-signal components. This field involves creating highly performant and efficient circuit blocks that are essential for a wide range of applications, from consumer electronics and communication systems to medical devices and automotive electronics. Researchers work on novel circuit design methods and techniques that improve power efficiency, and extend performance limitations for new electronic platforms.</p></div></div><div class="full"><div class="prof-pics"><div><img class="fac" src="/img/Asad_Abidi.jpg" alt="UCLA ECE Faculty Asad Abidi"></div><div><img class="fac" src="/img/Aydin-Babakhani.jpg" alt="UCLA ECE Faculty Aydin Babakhani"></div><div><img class="fac" src="/img/Frank_Chang.jpg" alt="UCLA ECE Faculty CESM.-C. Frank Chang"></div><div><img class="fac" src="/img/emaminejad6.jpg" alt="UCLA ECE Faculty Sam Emaminejad"></div><div><img class="fac" src="/img/gupta.jpg" alt="UCLA ECE Faculty Puneet Gupta"></div><div><img class="fac" src="/img/Lei.jpg" alt="UCLA ECE Faculty Lei He"></div><div><img class="fac" src="/img/Subramanian_Iyer.jpg" alt="UCLA ECE Faculty Subramanian S. Iyer"></div><div><img class="fac" src="/img/markovic5.jpg" alt="UCLA ECE Faculty Dejan Markovic"></div><div><img class="fac" src="/img/pamarti.jpg" alt="UCLA ECE Faculty Suhas Pamarti"></div><div><img class="fac" src="/img/razavi_behzad.jpg" alt="UCLA ECE Faculty Razavi Behzad"></div><div><img class="fac" src="/img/Villasenor.jpg" alt="UCLA ECE Faculty John Villasenor"></div><div><img class="fac" src="/img/CheeWeiWong3.jpg" alt="UCLA ECE Faculty Chee Wei Wong"></div><div><img class="fac" src="/img/Woo.jpg" alt="UCLA ECE Faculty Jason Woo"></div><div><img class="fac" src="/img/ckkenyang.jpg" alt="UCLA ECE Faculty C.-K. Ken Yang"></div></div><div class="page-caption faculty-caption"><b>Faculty: </b>Asad Abidi, Aydin Babakhani, -C. Frank Chang, Sam Emaminejad, Puneet Gupta, Lei He, Subramanian S. Iyer, Dejan Markovic, Suhas Pamarti, Razavi Behzad, John Villasenor, Chee Wei Wong, Jason Woo, C.-K. Ken Yang.</div></div></div><div class="grid-layout"><div class="full"><h3 class="page-title">Information/Optimization/Computation Theory</h3><p></p><div class="page-text"><p style="text-indent:0"><span class="first-letter">Exploring the mathematical </span> foundations and algorithms that underpin the efficient processing, transmission, and storage of information. This field delves into topics such as information theory, optimization algorithms, and computational complexity, aiming to develop new methods for solving complex problems in data communication, resource allocation, decision making, reliability and scalability. Researchers focus on creating theoretical frameworks and practical algorithms.</p></div></div><div class="full"><div class="prof-pics"><div><img class="fac" src="/img/Diggavi.jpg" alt="UCLA ECE Faculty Suhas Diggavi"></div><div><img class="fac" src="/img/Dolecek_Lara.jpg" alt="UCLA ECE Faculty Lara Dolecek "></div><div><img class="fac" src="/img/Fragouli-Teaching.jpg" alt="UCLA ECE Faculty  Christina Fragouli"></div><div><img class="fac" src="/img/Achuta-Kadambi.jpg" alt="UCLA ECE Faculty Achuta Kadabi"></div><div><img class="fac" src="/img/JonathanKao.jpg" alt="UCLA ECE Faculty  Jonathan Kao"></div><div><img class="fac" src="/img/Di-Luo.jpg" alt="UCLA ECE Faculty Di Luo"></div><div><img class="fac" src="/img/rahmat-samii04.jpg" alt="UCLA ECE Faculty Yahya Rahmat-Samii"></div><div><img class="fac" src="/img/Roychouwdhury.jpg" alt="UCLA ECE Faculty Vwani Roychouwdhury"></div><div><img class="fac" src="/img/Tabuada-Headshot.jpg" alt="UCLA ECE Faculty Paulo Tabuada"></div><div><img class="fac" src="/img/vandenberghe.jpg" alt="UCLA ECE Faculty Leiven Vandenberghe"></div><div><img class="fac" src="/img/Richard_Wesel.jpg" alt="UCLA ECE Faculty Richard Wesel"></div><div><img class="fac" src="/img/LinFYang.jpg" alt="UCLA ECE Faculty Lin Yang"></div><div><img class="fac" src="/img/leo-zhou.jpg" alt="UCLA ECE Faculty Leo Zhou"></div></div><div class="page-caption faculty-caption"><b>Faculty: </b> Suhas Diggavi, Lara Dolecek, Christina Fragouli, Achuta Kadabi, Jonathan Kao, Di Luo, Yahya Rahmat-Samii, Vwani Roychouwdhury, Paulo Tabuada, Leiven Vandenbergh, Richard Wesel, Lin Yang, Leo Zhou.</div></div></div><div class="grid-layout"><div class="full"><h3 class="page-title">Audio/Image Processing</h3><p></p><div class="page-text"><p style="text-indent:0"><span class="first-letter">Developing advanced techniques </span> for analyzing, manipulating, and interpreting audio and visual data. This field leverages machine learning in the creation of algorithms and systems that enhance the quality, efficiency, and functionality of audio and image processing. Applications range from speech recognition, audio enhancement, image compression, computer vision, and context interpretation. Researchers work on methods to enhance algorithms for better accuracy, applicability and user experiences.</p></div></div><div class="full"><div class="prof-pics"><div><img class="fac" src="/img/AbeerAlwan-profile.jpg" alt="UCLA ECE Faculty Abeer Alwan"></div><div><img class="fac" src="/img/Achuta-Kadambi.jpg" alt="UCLA ECE Faculty Achuta Kadabi"></div><div><img class="fac" src="/img/ozcan_nai3.jpg" alt="UCLA ECE Faculty Aydogan Ozcan"></div><div><img class="fac" src="/img/Roychouwdhury.jpg" alt="UCLA ECE Faculty Vwani Roychouwdhury"></div><div><img class="fac" src="/img/Villasenor.jpg" alt="UCLA ECE Faculty John Villasenor"></div></div><div class="page-caption faculty-caption"><b>Faculty: </b> Abeer Alwan, Achuta Kadabi, Aydogan Ozcan, Vwani Roychouwdhury, John Villasenor.</b></div></div></div></div> '
