    textCopy: ' <div><div class="grid-layout"><div class="full"><h3 class="page-title">Cyberphysical Systems, Control, and Robotics</h3><p></p><div class="page-text"><p style="text-indent:0"><span class="first-letter">Integration of computing, </span> networking, and physical processes to create intelligent, responsive systems. This field encompasses the design and control of robotics, autonomous systems, and smart infrastructure, where sensors, actuators, and computational algorithms work together to interact with the physical world in real-time. Researchers focus on developing advanced control strategies, ensuring system stability, and enhancing the autonomy and efficiency of these systems. Applications range from industrial automation and healthcare robotics to smart cities and transportation.</p></div></div><div class="full"><div class="prof-pics"><div><img class="fac" src="/img/candler2.jpg" alt="UCLA ECE Faculty Robert Candler"></div><div><img class="fac" src="/img/Xiaofan-Cui-portrait.jpg" alt="UCLA ECE Faculty Xiaofan Cui"></div><div><img class="fac" src="/img/Diggavi.jpg" alt="UCLA ECE Faculty Suhas Diggavi"></div><div><img class="fac" src="/img/emaminejad6.jpg" alt="UCLA ECE Faculty Sam Emaminejad"></div><div><img class="fac" src="/img/Fragouli-Teaching.jpg" alt="UCLA ECE Faculty  Christina Fragouli"></div><div><img class="fac" src="/img/Achuta-Kadambi.jpg" alt="UCLA ECE Faculty Achuta Kadabi"></div><div><img class="fac" src="/img/JonathanKao.jpg" alt="UCLA ECE Faculty  Jonathan Kao"></div><div><img class="fac" src="/img/Ankur_Mehta.jpg" alt="UCLA ECE Faculty  Ankur Mehta"></div><div><img class="fac" src="/img/Roychouwdhury.jpg" alt="UCLA ECE Faculty Vwani Roychouwdhury"></div><div><img class="fac" src="/img/Nader-Sehatbakhsh.jpg" alt="UCLA ECE Faculty Nader Sehatbakhsh"></div><div><img class="fac" src="/img/Mani-Srivastava.jpg" alt="UCLA ECE Faculty Mani Sirivastava"></div><div><img class="fac" src="/img/Tabuada-Headshot.jpg" alt="UCLA ECE Faculty Paulo Tabuada"></div><div><img class="fac" src="/img/Yuan-Tian.jpg" alt="UCLA ECE Faculty Yuan Tian"></div><div><img class="fac" src="/img/vandenberghe.jpg" alt="UCLA ECE Faculty Lieven Vandenberghe"></div><div><img class="fac" src="/img/Villasenor.jpg" alt="UCLA ECE Faculty John Villasenor"></div></div><div class="page-caption faculty-caption"><b>Faculty:</b>Robert Candler, Xiaofan Cui, Suhas Diggavi, Sam Emaminejad, Christina Fragouli, Achuta Kadabi, Jonathan Kao, Ankur Mehta, Vwani Roychouwdhury, Nader Sehatbakhsh, Mani Sirivastava, Paulo Tabuada, Yuan Tian, Lieven Vandenberghe, John Villasenor.</div></div><div class="spacer"><img src="/img/pixel.png"></div></div><div class="grid-layout"><div class="full"><h3 class="page-title">Data Communication and Networking</h3><p></p><div class="page-text"><p style="text-indent:0"><span class="first-letter">Design, analysis, and optimization </span> of systems that enable the reliable and efficient mobile computing experiences, exchange of data across networks, and enable the seamless integration of smart devices within the Internet of Things (IoT) ecosystem.. This field covers a broad range of topics, including network protocols, wireless communication, internet architecture, network security, and efficient mobile applications that respond to real-time data. Researchers work on developing new techniques to enhance the speed, capacity, and security of data transmission, addressing challenges in areas such as 5G/6G networks, the Internet of Things (IoT), and cloud computing. The goal is to create robust and scalable communication networks that provide pervasive accessibility and connectivity.</p></div></div><div class="full"><div class="prof-pics"><div><img class="fac" src="/img/Cabric.jpg" alt="UCLA ECE Faculty SS Danijela Cabric"></div><div><img class="fac" src="/img/Diggavi.jpg" alt="UCLA ECE Faculty Suhas Diggavi"></div><div><img class="fac" src="/img/Dolecek_Lara.jpg" alt="UCLA ECE Faculty Lara Dolecek"></div><div><img class="fac" src="/img/Fragouli-Teaching.jpg" alt="UCLA ECE Faculty  Christina Fragouli"></div><div><img class="fac" src="/img/Liz-Izhikevich.jpg" alt="UCLA ECE Faculty Liz Izhikevich"></div><div><img class="fac" src="/img/pamarti.jpg" alt="UCLA ECE Faculty Pamarti"></div><div><img class="fac" src="/img/ian-roberts-portrait.jpg" alt="UCLA ECE Faculty Ian Roberts"></div><div><img class="fac" src="/img/Roychouwdhury.jpg" alt="UCLA ECE Faculty Vwani Roychouwdhury"></div><div><img class="fac" src="/img/Mani-Srivastava.jpg" alt="UCLA ECE Faculty Mani Sirivastava"></div><div><img class="fac" src="/img/Villasenor.jpg" alt="UCLA ECE Faculty John Villasenor"></div><div><img class="fac" src="/img/Richard_Wesel.jpg" alt="UCLA ECE Faculty Richard Wesel"></div><div><img class="fac" src="/img/CheeWeiWong3.jpg" alt="UCLA ECE Faculty Chee Wei Wong"></div><div><img class="fac" src="/img/ckkenyang.jpg" alt="UCLA ECE Faculty  C.-K. Ken Yang "></div><div><img class="fac" src="/img/yangzhang.jpg" alt="UCLA ECE Faculty Yang Zhang"></div></div><div class="page-caption faculty-caption"><b>Faculty:</b>Danijela Cabric, Suhas Diggavi, Lara Dolecek, Christina Fragouli, Liz Izhikevich, Suhas Pamarti, Ian Roberts, Vwani Roychouwdhury, Mani Sirivastava, John Villasenor, Richard Wesel, Chee Wei Wong, C.-K. Ken Yang, Yang Zhang.</div></div><div class="spacer"><img src="/img/pixel.png"></div></div><div class="grid-layout"><div class="full"><h3 class="page-title">Photonics, Optics, and Plasma Electronics</h3><p></p><div class="page-text"><p style="text-indent:0"><span class="first-letter">Application of light </span> and plasma phenomena for advanced technology development. This field encompasses the design and optimization of optical systems, including lasers, fiber optics, and imaging technologies. Researchers investigate innovative approaches to harness and manipulate light for applications such as high-speed data transmission, imaging, sensing, and energy conversion. Advances in this area drive progress in diverse fields, including telecommunications, medical devices, and detection/monitoring.</p></div></div><div class="full"><div class="prof-pics"><div><img class="fac" src="/img/candler2.jpg" alt="UCLA ECE Faculty Rob Candler"></div><div><img class="fac" src="/img/SergioCarbajo.jpg" alt="UCLA ECE Faculty Sergio Carbajo"></div><div><img class="fac" src="/img/Frank_Chang.jpg" alt="UCLA ECE Faculty CESM.-C. Frank Chang"></div><div><img class="fac" src="/img/Mona-Jarrahi.jpg" alt="UCLA ECE Faculty Mona Jarrahi"></div><div><img class="fac" src="/img/Joshi.jpg" alt="UCLA ECE Faculty Chandrashekhar Joshi"></div><div><img class="fac" src="/img/Jia-Ming-Liu.jpg" alt="UCLA ECE Faculty  Jia Ming Liu"></div><div><img class="fac" src="/img/Di-Luo.jpg" alt="UCLA ECE Faculty Di Luo"></div><div><img class="fac" src="/img/ozcan_nai3.jpg" alt="UCLA ECE Faculty Aydogan Ozcan"></div><div><img class="fac" src="/img/wang.jpg" alt="UCLA ECE Faculty Kang L.Wang"></div><div><img class="fac" src="/img/williams.jpg" alt="UCLA ECE Faculty  Benjamin Williams"></div><div><img class="fac" src="/img/CheeWeiWong3.jpg" alt="UCLA ECE Faculty Chee Wei Wong"></div></div><div class="page-caption faculty-caption"><b>Faculty:</b>Rob Candler, Sergio Carbajo,-C. Frank Chang, Mona Jarrahi, Chandrashekhar Joshi, Jia Ming Liu, Di Luo, Aydogan Ozcan, Kang L. Wang, Benjamin Williams,Chee Wei Wong"></div></div><div class="spacer"><img src="/img/pixel.png"></div></div><div class="grid-layout"><div class="full"><h3 class="page-title">Quantum Science</h3><p></p><div class="page-text"><p style="text-indent:0"><span class="first-letter">Exploration and application of quantum </span> effects toward real-world applications. This field involves quantum computing platforms, novel quantum materials, quantum sensing and quantum information theory to innovate computing efficiency, and sensors. Researchers aim to harness the unique properties of quantum systems, such as superposition and entanglement, to solve complex problems beyond the capabilities of classical systems. This research area focusses on the enablement of theories toward real applications in revolutionizing computing, enhancing secure communications, and enabling precise measurements.</p></div></div><div class="full"><div class="prof-pics"><div><img class="fac" src="/img/Elaheh-Ahmadi.jpg" alt="UCLA ECE Faculty Elaheh Ahmadi"></div><div><img class="fac" src="/img/candler2.jpg" alt="UCLA ECE Faculty Rob Candler"></div><div><img class="fac" src="/img/SergioCarbajo.jpg" alt="UCLA ECE Faculty Sergio Carbajo"></div><div><img class="fac" src="/img/Dolecek_Lara.jpg" alt="UCLA ECE Faculty Lara Dolecek"></div><div><img class="fac" src="/img/Joshi.jpg" alt="UCLA ECE Faculty Chandrashekhar Joshi"></div><div><img class="fac" src="/img/Di-Luo.jpg" alt="UCLA ECE Faculty Di Luo"></div><div><img class="fac" src="/img/Roychouwdhury.jpg" alt="UCLA ECE Faculty Vwani Roychouwdhury"></div><div><img class="fac" src="/img/wang.jpg" alt="UCLA ECE Faculty Kang L.Wang"></div><div><img class="fac" src="/img/williams.jpg" alt="UCLA ECE Faculty  Benjamin Williams"></div><div><img class="fac" src="/img/CheeWeiWong3.jpg" alt="UCLA ECE Faculty Chee Wei Wong"></div><div><img class="fac" src="/img/leo-zhou.jpg" alt="UCLA ECE Faculty Leo Zhou"></div></div><div class="page-caption faculty-caption"><b>Faculty:</b>Elaheh Ahmadi, Rob Candler, Sergio Carbajo, Lara Dolecek, Chandrashekhar Joshi, Di Luo, Vwani Roychouwdhury, Kang L.Wang, Benjamin Williams, Chee Wei Wong,Leo Zhou</div><div class="spacer"><img src="/img/pixel.png"></div></div></div></div> '
