    textCopy: '  <div><div class="grid-layout"><div class="full"><h3 class="page-title">Semiconductors and Packaging Technology</h3><p></p><div class="page-text"><p style="text-indent:0"><span class="first-letter">Advancing the design,</span> fabrication, and integration of semiconductor devices through innovation of new devices, application of new materials, and novel methods of 2.5D/3D integration. This field involves exploring materials, processes, and design methods to improve the performance, and continued scaling of electronic components and their capability. This field is critical for driving progress in consumer electronics, and computing systems. Researchers work on developing cutting-edge semiconductor materials, and optimizing device architectures. Researchers also explore innovative packaging solutions that ensure effective thermal management, electrical connectivity, and mechanical protection.</p></div></div><div class="full"><div class="prof-pics"><div><img class="fac" src="/img/Elaheh-Ahmadi.jpg" alt="UCLA ECE Faculty Elaheh Ahmadi"></div><div><img class="fac" src="/img/candler2.jpg" alt="UCLA ECE Faculty Rob Candler"></div><div><img class="fac" src="/img/Frank_Chang.jpg" alt="UCLA ECE Faculty CESM.-C. Frank Chang"></div><div><img class="fac" src="/img/emaminejad6.jpg" alt="UCLA ECE Faculty Sam Emaminejad"></div><div><img class="fac" src="/img/gupta.jpg" alt="UCLA ECE Faculty Puneet Gupta"></div><div><img class="fac" src="/img/Subramanian_Iyer.jpg" alt="UCLA ECE Faculty Subramanian S. Iyer"></div><div><img class="fac" src="/img/pamarti.jpg" alt="UCLA ECE Faculty Suhas Pamarti"></div><div><img class="fac" src="/img/wang.jpg" alt="UCLA ECE Faculty Kang L. Wang"></div><div><img class="fac" src="/img/williams.jpg" alt="UCLA ECE Faculty  Benjamin Williams"></div><div><img class="fac" src="/img/CheeWeiWong3.jpg" alt="UCLA ECE Faculty Chee Wei Wong"></div><div><img class="fac" src="/img/Woo.jpg" alt="UCLA ECE Faculty Jason Woo"></div><div><img class="fac" src="/img/ckkenyang.jpg" alt="UCLA ECE Faculty  C.-K. Ken Yang"></div></div><div class="page-caption faculty-caption"><b>Faculty:</b>Elaheh Ahmadi, Rob Candler, -C. Frank Chang, Sam Emaminejad, Puneet Gupta, Subramanian S. Iyer, Suhas Pamarti, Kang L. Wang, Benjamin Williams, Chee Wei Wong, Jason Woo, C.-K. Ken Yang.</div><div class="spacer"><img src="/img/pixel.png"></div></div></div><div class="grid-layout"><div class="full"><h3 class="page-title">Electromagnetics-Antennas-Microwave and mmWave Systems</h3><p></p><div class="page-text"><p style="text-indent:0"><span class="first-letter">Design and analysis of </span> electromagnetic systems operating at microwave and millimeter-wave frequencies. This field includes the development of advanced antennas, waveguides, and communication systems that operate at high frequencies to enable faster data transmission and improved signal processing. Researchers explore innovative techniques in electromagnetic theory, materials, and circuit design to enhance the performance and efficiency of these systems. Applications span from high-speed wireless communications and radar systems to satellite technology and advanced imaging.</p></div></div><div class="full"><div class="prof-pics"><div><img class="fac" src="/img/Elaheh-Ahmadi.jpg" alt="UCLA ECE Faculty Elaheh Ahmadi"></div><div><img class="fac" src="/img/Aydin-Babakhani.jpg" alt="UCLA ECE Faculty Aydin Babakhani"></div><div><img class="fac" src="/img/candler2.jpg" alt="UCLA ECE Faculty Rob Candler"></div><div><img class="fac" src="/img/Frank_Chang.jpg" alt="UCLA ECE Faculty CESM.-C. Frank Chang"></div><div><img class="fac" src="/img/emaminejad6.jpg" alt="UCLA ECE Faculty Sam Emaminejad"></div><div><img class="fac" src="/img/Subramanian_Iyer.jpg" alt="UCLA ECE Faculty Subramanian S. Iyer"></div><div><img class="fac" src="/img/Mona-Jarrahi.jpg" alt="UCLA ECE Faculty Mona Jarrahi"></div><div><img class="fac" src="/img/Ozcan_Aydogan.jpg" alt="UCLA ECE Faculty Aydogan Ozcan"></div><div><img class="fac" src="/img/rahmat-samii04.jpg" alt="UCLA ECE Faculty Yahya Rahmat-Samii"></div><div><img class="fac" src="/img/wang.jpg" alt="UCLA ECE Faculty Kang L. Wang"></div><div><img class="fac" src="/img/ethanwang.jpg" alt="UCLA ECE Faculty Ethan Wang"></div><div><img class="fac" src="/img/williams.jpg" alt="UCLA ECE Faculty  Benjamin Williams"></div><div><img class="fac" src="/img/CheeWeiWong3.jpg" alt="UCLA ECE Faculty Chee Wei Wong"></div></div><div class="page-caption faculty-caption"><b>Faculty :</b>Elaheh Ahmadi, Aydin Babakhani, Rob Candler, -C. Frank Chang, Sam Emaminejad, Subramanian S. Iyer, Mona Jarrahi, Aydogan Ozcan, Yahya Rahmat-Samii, Kang L. Wang, Ethan Wang, Benjamin Williams, Chee Wei Wong.</div></div><div class="spacer"><img src="/img/pixel.png"></div></div><div class="grid-layout"><div class="full"><h3 class="page-title">Biomedical Systems and Applications</h3><p></p><div class="page-text"><p style="text-indent:0"><span class="first-letter">Leveraging engineering principles </span> to advance healthcare technologies and improve medical diagnosis. This field involves the development and integration of innovative systems and devices, such as medical imaging tools, wearable health monitors, and diagnostic algorithms, to support patient care and medical research. Researchers work on enhancing the accuracy, efficiency, and functionality of biomedical systems, aiming to provide solutions for early disease detection, personalized treatment, and health management.</p></div></div><div class="full"><div class="prof-pics"><div><img class="fac" src="/img/Aydin-Babakhani.jpg" alt="UCLA ECE Faculty Aydin Babakhani"></div><div><img class="fac" src="/img/candler2.jpg" alt="UCLA ECE Faculty Rob Candler"></div><div><img class="fac" src="/img/Diggavi.jpg" alt="UCLA ECE Faculty Suhas Diggavi"></div><div><img class="fac" src="/img/emaminejad6.jpg" alt="UCLA ECE Faculty Sam Emaminejad"></div><div><img class="fac" src="/img/Subramanian_Iyer.jpg" alt="UCLA ECE Faculty Subramanian S. Iyer"></div><div><img class="fac" src="/img/Achuta-Kadambi.jpg" alt="UCLA ECE Faculty Achuta Kadabi"></div><div><img class="fac" src="/img/JonathanKao.jpg" alt="UCLA ECE Faculty Jonathan Kao"></div><div><img class="fac" src="/img/markovic5.jpg" alt="UCLA ECE Faculty Dejan Markovic"></div><div><img class="fac" src="/img/ozcan_nai3.jpg" alt="UCLA ECE Faculty Aydogan Ozcan"></div><div><img class="fac" src="/img/rahmat-samii04.jpg" alt="UCLA ECE Faculty Yahya Rahmat-Samii"></div><div><img class="fac" src="/img/Roychouwdhury.jpg" alt="UCLA ECE Faculty Vwani Roychouwdhury"></div><div><img class="fac" src="/img/Mani-Srivastava.jpg" alt="UCLA ECE Faculty Mani Sirivastava"></div><div><img class="fac" src="/img/Yuan-Tian.jpg" alt="UCLA ECE Faculty Yuan Tian"></div></div><div class="page-caption faculty-caption"><b>Faculty:</b>Aydin Babakhani, Rob Candler, Suhas Diggavi, Sam Emaminejad, Subramanian S. Iyer, Achuta Kadabi, Jonathan Kao, Dejan Markovic, Aydogan Ozcan, Yahya Rahmat-Samii, Vwani Roychouwdhury, Mani Sirivastava, Yuan Tian .</div></div><div class="spacer"><img src="/img/pixel.png"></div></div></div>   '
