    textCopy: ' <div class="grid-cards"><h3 class="page-title"></h3><div class="card-deck"><div class="card"><div class="frame"><img class="card-pic" src="/img/Asad_Abidi2.jpg" alt="UCLA academy abidi"></div><div class="prof-title">Distinguished<br>Chancellor\'s Professor</div><div class="p-name">Asad A. Abidi</div><ul class="card-bottom"><li>National Academy of Engineering, 2007.</li><li>For his contributions to the development of MOS integrated circuits for RF Communications.</li><li></li></ul></div><div class="card"><div class="frame"><img class="card-pic" src="/img/Frank_Chang.jpg" alt="UCLA academy frank chang"></div><div class="prof-title">Distinguished<br>Professor</div><div class="p-name">Mau-Chung Frank Chang</div><ul class="card-bottom"><li>National Academy of Engineering, 2008.</li><li>National Academy of Inventors, 2015.</li><li>For his contributions in development and commercialization of III-V-based heterojunction bipolar transistors (HBTs) and field-effective transistors (FETs) for RF wireless communications.</li><li></li></ul></div><div class="card"><div class="frame"><img class="card-pic" src="/img/Jason_Cong.jpg" alt="UCLA academy cong"></div><div class="prof-title">Distinguished<br>Chancellor\'s Professor</div><div class="p-name">Jason Cong</div><ul class="card-bottom"><li>National Academy of Engineering, 2017.</li><li>For pioneering contributions to application-specific programmable logic via innovations in field-programmable gate array synthesis.</li><li>National Academy of Inventors, 2020.</li><li></li></ul></div><div class="card"><div class="frame"><img class="card-pic" src="/img/DariushDivsalar.jpg" alt="UCLA academy Divasalar"></div><div class="prof-title">Adjunct<br>Professor</div><div class="p-name">Dariush Divsalar</div><ul class="card-bottom"><li>National Academy of Engineering, 2024.</li><li>Best Joint Paper Award, IEEE Information Theory/IEEE Communication Theory Societies. 2008.</li><li>Best Paper Award: "A simple tight bound on error probability of block codes with application to turbo codes." Jet Propulsion Laboratory, NASA, 2000.</li><li>Hugh Fosque Best Paper Award: "Code Performance as a function of block size", Jet Propulsion Laboratory, NASA, 1989.</li><li>Best Paper Award, IEEE Transactions on Vehicular Technology, 1988.</li><li></li></ul></div><div class="card"><div class="frame"><img class="card-pic" src="/img/Goebel.jpg" alt="UCLA academy goebel"></div><div class="prof-title">Adjunct<br>Professor</div><div class="p-name">Dan Goebel</div><ul class="card-bottom"><li>National Academy of Engineering, 2015.</li><li>National Academy of Inventors, 2016.</li><li>Senior Research Scientist, NASA\'s Jet Propulsion Laboratory. For contributions to low-temperature plasma sources for thin-film manufacturing, plasma materials interactions, and electric propulsion.</li><li></li></ul></div><div class="card"><div class="frame"><img class="card-pic" src="/img/Subramanian_Iyer.jpg" alt="UCLA academy iyer"></div><div class="prof-title">Distinguished<br>Chancellor\'s Professor</div><div class="p-name">Subramanian Iyer</div><ul class="card-bottom"><li>National Academy of Inventors, 2017.</li><li>He is also a Fellow of IEEE and the American Physical Society.</li><li></li></ul></div><div class="card"><div class="frame"><img class="card-pic" src="/img/Jalali.jpg" alt="UCLA academy Jalali"></div><div class="prof-title">Distinguished<br>Professor</div><div class="p-name">Bahram Jalali</div><ul class="card-bottom"><li>National Academy of Enginering, 2022.</li><li>National Academy of Inventors, 2020.</li><li>He is also a member of AIMBE.</li><li></li></ul></div><div class="card"><div class="frame"><img class="card-pic" src="/img/Joshi.jpg" alt="UCLA academy Joshi"></div><div class="prof-title">Distinguished<br>Professor</div><div class="p-name">Chandrashekhar Joshi</div><ul class="card-bottom"><li>National Academy of ­Engineering, 2014.</li><li>Founder of the experimental field of plasma accelerators. He is also a Fellow of the American Physical Society, IEEE and the ­Institute of Physics.</li><li></li></ul></div><div class="card"><div class="frame"><img class="card-pic" src="/img/Liou_Kuo-Nan.jpg" alt="UCLA academy Liou Kuo-Nan"></div><div class="prof-title">Professor</div><div class="p-name">Kuo-Nan Liou</div><ul class="card-bottom"><li>National Academy of Engineering, 1999.</li><li>Director of the Joint ­Institute for Regional Earth System Science and Engineering.</li><li>Nobel Peace Prize, 2007, shared with ­Intergovernmental Panel on ­Climate Change.</li></ul></div><div class="card"><div class="frame"><img class="card-pic" src="/img/Asad-Madni2.jpg" alt="UCLA academy Asad Madni"></div><div class="prof-title">Distinguished<br>Adj. Professor</div><div class="p-name">­ Asad M. Madni</div><ul class="card-bottom"><li>Canadian Academy of Engineering, 2021.</li><li>Royal Academy of Engineering 2020.</li><li>National Academy of Inventors, 2015.</li><li>National Academy of Engineering 2011.</li><li>Former President, COO and CTO of BEI Technologies Inc. He is a Fellow of the IEEE, IET, AAAS, NYAS, WAS, SAE, IAE, AIAA, RAeS, AIMBE and AAIA.</li><li></li></ul></div><div class="card"><div class="frame"><img class="card-pic" src="/img/AliMosleh.jpg" alt="UCLA academy Ali Mosleh"></div><div class="prof-title">Distinguished<br>Professor</div><div class="p-name">­ Ali Mosleh</div><ul class="card-bottom"><li>National Academy of Engineering, 2010.</li><li>For contributions to the development of Bayesian methods and computational tools in probabilistic risk assessment and reliability engineering. Fellow, American Nuclear Society, 2013 and Society for Risk Analysis, 1999</li><li></li></ul></div><div class="card"><div class="frame"><img class="card-pic" src="/img/StanleyOsher.jpg" alt="UCLA academy Stanley Osher"></div><div class="prof-title">Professor</div><div class="p-name">­Stanley Osher</div><ul class="card-bottom"><li>National Academy of ­Sciences, 2005.</li><li>National Academy of ­Engineering, 2018.</li><li>For contributions to imaging, computer vision, and graphics including level-set methods and efficient compressed sensing.</li><li></li></ul></div><div class="card"><div class="frame"><img class="card-pic" src="/img/ozcan_nai3.jpg" alt="UCLA academy Ozcan"></div><div class="prof-title">Professor</div><div class="p-name">Aydogan Ozcan</div><ul class="card-bottom"><li>National Academy of Engineering, 2025.</li><li>National Academy of Inventors, 2019.</li><li>He is also a Fellow of IEEE 2017, AIMBE, AAAS, SPIE and the American Physical Society.</li><li></li></ul></div><div class="card"><div class="frame"><img class="card-pic" src="/img/PATEL-emeritus.jpg" alt="UCLA academy Patel"></div><div class="prof-title">Distinguished Professor</div><div class="p-name">C. Kumar Patel</div><ul class="card-bottom"><li>National Academy of Inventors, 2012.</li><li>National Academy of Engineering, 1978.</li><li>National Academy of ­Sciences, 1974.</li><li>Made ­numerous contributions in gas lasers, nonlinear ­optics, ­molecular­spectroscopy, ­pollution detection and laser surgery.</li><li></li></ul></div><div class="card"><div class="frame"><img class="card-pic" src="/img/pottie.jpg" alt="UCLA academy Greg Pottie"></div><div class="prof-title">Professor</div><div class="p-name">Greg Pottie</div><ul class="card-bottom"><li>Senior Member of National Academy of Inventors, 2017.</li><li>He is also a Fellow of IEEE and former Department Chair.</li><li></li></ul></div><div class="card"><div class="frame"><img class="card-pic" src="/img/rahmat-samii04.jpg" alt="UCLA academy Yahya Rahmat-Samii"></div><div class="prof-title">Distinguished<br>Professor</div><div class="p-name">­ Yahya Rahmat-Samii</div><ul class="card-bottom"><li>National Academy of Engineering, 2008.</li><li>For contributions to design and measurement of reflector and hand-held devices antennas.</li><li></li></ul></div><div class="card"><div class="frame"><img class="card-pic" src="/img/razavi_behzad.jpg" alt="UCLA academy ­Behzad Razavi"></div><div class="prof-title">Professor</div><div class="p-name">­ ­Behzad Razavi</div><ul class="card-bottom"><li>National Academy of Engineering, 2017.</li><li>For contributions to low-power broadband communication circuits. He is the ­director of the ­Communication Circuits Laboratory.</li><li></li></ul></div><div class="card"><div class="frame"><img class="card-pic" src="/img/Henry-Samueli.jpg" alt="UCLA academy Henry Samueli"></div><div class="prof-title">Professor</div><div class="p-name">­ Henry Samueli</div><ul class="card-bottom"><li>National Academy of Inventors, 2017.</li><li>National Academy of Engineering, 2003.</li><li>He is co-founder of Broadcom and ­recipient of the Marconi Prize, 2012, and UCLA Medal, 2010.</li><li></li></ul></div><div class="card"><div class="frame"><img class="card-pic" src="/img/Sayed.jpg" alt="UCLA academy sayed"></div><div class="prof-title">Distinguished<br>Professor</div><div class="p-name">­ Ali Sayed</div><ul class="card-bottom"><li>National Academy of Engineering, 2018,</li><li>For contributions to the theory and applications of adaptive signal processing.</li><li></li></ul></div><div class="card"><div class="frame"><img class="card-pic" src="/img/Speyer.jpg" alt="UCLA academy Jason Speyer"></div><div class="prof-title">Professor</div><div class="p-name">­ Jason Speyer</div><ul class="card-bottom"><li>National Academy of Engineering, 2005.</li><li>For the development and application of ­advanced techniques for optimal ­navigation and control of a wide range of aerospace vehicles.</li><li></li></ul></div><div class="card"><div class="frame"><img class="card-pic" src="/img/DwightStreit.jpg" alt="UCLA academy Dwight Streit"></div><div class="prof-title">Professor</div><div class="p-name">­ Dwight Streit</div><ul class="card-bottom"><li>National Academy of Engineering, 2001.</li><li>For contributions to the ­development and production of heterojunction transistors and circuits.</li><li></li></ul></div><div class="card"><div class="frame"><img class="card-pic" src="/img/Gabor_C_Temes.jpg" alt="UCLA academy Gabor Temes"></div><div class="prof-title">Distinguished<br>Professor ­Emeritus</div><div class="p-name">­ Gabor Temes</div><ul class="card-bottom"><li>National Academy of Inventors, 2019.</li><li>National Academy of Engineering, 2015.</li><li>For his contributions to analog signal processing and engineering education.</li><li></li></ul></div><div class="card"><div class="frame"><img class="card-pic" src="/img/wang.jpg" alt="UCLA academy Kang Wang"></div><div class="prof-title">Distinguished Professor</div><div class="p-name">­ Kang Wang</div><ul class="card-bottom"><li>National Academy of Inventors, 2022.</li><li>He is a fellow of the American Physical Society and IEEE, a Guggenheim Fellow and a member of Academia Sinica in Taiwan.</li><li></li></ul></div><div class="card"><div class="frame"><img class="card-pic" src="/img/Alan-Willson.jpg" alt="UCLA academy Alan N. Willson"></div><div class="prof-title">Professor Emeritus</div><div class="p-name">­ Alan N. Willson, Jr.</div><ul class="card-bottom"><li>National Academy of Inventors, 2018.</li><li>National Academy of Engineering, 2014.</li><li>For contributions to the theory and ­applications of digital signal ­processing.</li><li></li></ul></div><div class="card"><div class="frame"><img class="card-pic" src="/img/CheeWeiWong3.jpg" alt="UCLA academy Chee Wei Wong"></div><div class="prof-title">Professor</div><div class="p-name">­ Chee Wei Wong</div><ul class="card-bottom"><li>National Academy of Inventors, 2020.</li><li>He is also a Fellow of IEEE, SPIE, American Society of Mechanical Engineers and the American Physical Society.</li><li></li></ul></div><div class="card"><div class="frame"><img class="card-pic" src="/img/Eli-Yablonovitch.jpg" alt="UCLA academy Eli Yablonovitch"></div><div class="prof-title">Professor</div><div class="p-name">­ Eli Yablonovitch</div><ul class="card-bottom"><li>National Academy of Inventors, 2018.</li><li>National Academy of Engineering, 2003.</li><li>National Academy of Sciences, 2003.</li><li>He\'s also a foreign member of the Royal Society of London.</li><li></li></ul></div><div class="card"><div class="frame"><img class="card-pic" src="/img/Lixia-Zhang.jpg" alt="UCLA NAE academy ECE Faculty Lixia Zhang"></div><div class="prof-title">Professor</div><div class="p-name">Lixia Zhang</div><ul class="card-bottom"><li>National Academy of Engineering, 2025.</li><li>Internet Hall of Fame Member, 2021.</li><li>ACM Fellow.</li><li>IEEE Fellow.</li><li></li></ul></div></div></div> '
