    textCopy: ' <div class="grid-cards"><h3 class="page-title">Henry Samueli Received the IEEE 2025 Medal of Honor</h3><div class="page-text"><p style="text-indent:0">Henry Samueli is the 2025 IEEE Medal of Honor Laureate, recognized for developing communications products that have touched virtually every person in the world. He was presented with his medal at the IEEE Honors Ceremony on 24 April 2025 in Tokyo, Japan. An IEEE-Eta Kappa Nu Eminent Member, Samueli has given the $2M prize to fund a permanent endowment for Eta Kappa Nu. This permanent endowment will further his commitment to developing future leaders.</p><p>The IEEE Medal of Honor is the highest IEEE award, and one of the world’s most prestigious technology-focused prizes. It was established in 1917 to recognize key advances in radio technologies by the greatest minds of our time for work that benefits humanity. Effective in 2025, IEEE increased the IEEE Medal of Honor monetary prize to $2 million because we are at an inflection point globally – where technology, engineering, and science have become integral to addressing global challenges such as climate change and positively moving our world forward. In the evaluation process, the significance of achievement, originality, impact on society, impact on the profession, publications and patents relating to the achievement, and quality of nomination are the top criteria.</p></div><div class="card-deck"><div class="card"><div class="frame"><img class="card-pic" src="/2023-2025/img/Henry_Samueli_Graymark506B.jpg" alt="UCLA ECE Faculty Henry Samueli with Greymark radio kit he owned in middle schoool"></div><div class="prof-title">Professor</div><div class="p-name">Henry Samueli</div><ul class="card-bottom"><li>2025, IEEE 2025 Medal of Honor</li><li>2017, Fellow of the National Academy of Inventors</li><li>2012, Marconi Society Prize and Fellowship</li><li>2010, UCLA Medal</li><li>2004, Fellow of the American Academy of Arts and Sciences</li><li>2003, National Academy of Engineering</li><li>2000, IEEE Fellow</li><li></li><li>In the News: IEEE Spectrum</li><li>https://spectrum.ieee.org/henry-samueli-moh</li><li></li></ul></div></div><div class="spacer"><img src="/img/pixel.png"></div></div> '
